* {
   margin: 0;
   padding: 0;
   box-sizing: border-box; }

body {
   // overflow-x: hidden
   font-family: Arial, Helvetica, sans-serif; }

img {
   display: block;
   max-width: 100%; }

.wrapper {
   margin: 0 auto;
   width: 90%;
   max-width: 1200px; }

h1.header-title {
   font-family: 'DM Serif Display', serif;
   font-size: 36px;
   color: white;
   text-shadow: 0 0 2px black;
   @media (min-width: 768px) {
      font-size: 4.2vw;
      @media (min-width: 1200px) {
         font-size: 54px; } } }

h2.header-title {
   font-family: 'Lato', sans-serif;
   font-size: 20px;
   color: white;
   text-shadow: 0 0 2px black; }

h1.section-title {
   font-family: 'DM Serif Display', serif;
   font-size: 32px;
   text-align: center; }

h2.section-title, figcaption {
   font-family: 'DM Serif Display', serif;
   font-size: 24px; }

// h3.section-title
//    font-family: 'Lato', sans-serif;
//    font-size: 24px
//    color: $c-gray-dark

p {
   font-family: 'Lato', sans-serif;
   font-size: 20px;
   color: $c-gray-dark;
   line-height: 1.5; }

h1 span, h2 span, h3 span, p span {
   color: $c-pink-light; }
